:root {
  --light1: #dcdcdc;
  --light2: #d0d0d0;
  --light3: #8f8e8e;
  --dark1: #393939;
  --dark2: #2a2a2a;
  --dark3: #202020;
  --padding: 10px;
  --rounding: 5px;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--dark3);
}

::-webkit-scrollbar-thumb {
  background: var(--dark1);
  border-radius: var(--rounding);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--light3);
}

svg path {
  fill: var(--light1);
}

html {
  background-color: var(--dark3);
  color: #fff;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Noto Sans, serif;
  font-size: large;
  display: flex;
}

body {
  box-sizing: border-box;
  width: 80vw;
  margin: 0;
  box-shadow: 0 0 10vw #0606068d;
}

.inputWrapper {
  background-color: var(--dark1);
  gap: var(--padding);
  padding: var(--padding);
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  display: flex;
}

.searchContainer {
  background-color: var(--dark1);
  gap: var(--padding);
  flex-direction: row;
  height: 30vh;
  display: flex;
}

.visualPianoContainer {
  background-color: var(--dark2);
  border-radius: var(--padding);
  flex-direction: row;
  flex: 1.3;
  align-items: end;
  padding: 5px;
  display: flex;
}

.visualPianoClear {
  width: min(5vh, 10vw);
  height: auto;
  transition: all .2s;
}

.visualPianoClear:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.visualPianoClear:active {
  cursor: pointer;
  transform: scale(.8);
}

.pianoContainer {
  border-radius: .62vw;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  box-shadow: 0 2vw 4vw #07003540;
}

.searchWordContainer {
  background-color: var(--dark2);
  border-radius: var(--padding);
  flex-direction: column;
  flex: 1;
  height: 100%;
  display: flex;
  position: relative;
  overflow: hidden;
}

.blackFadeBottom {
  border-radius: var(--padding);
  z-index: 3;
  pointer-events: none;
  background: linear-gradient(0deg, #1f1f1f 0%, #3f3f3f00 10%, #0000 100%);
  width: 100%;
  height: 100%;
  position: absolute;
}

.searchBox {
  background-color: var(--dark2);
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid var(--dark1);
  padding: calc(var(--padding) / 2) var(--padding);
  border-radius: var(--rounding) var(--rounding) 0 0;
  color: var(--light1);
  width: 100%;
  height: auto;
  font-size: large;
}

.searchBox:focus {
  outline: none;
}

.searchChordList {
  box-sizing: border-box;
  padding: var(--padding);
  -webkit-user-select: none;
  user-select: none;
  flex-flow: wrap;
  flex-grow: 0;
  align-content: flex-start;
  gap: 10px;
  height: 100%;
  max-height: 30vh;
  display: flex;
  overflow-y: auto;
}

.selectedItems {
  place-content: flex-start center;
  gap: var(--padding);
  box-sizing: border-box;
  padding: var(--padding);
  background-color: var(--dark2);
  border-radius: var(--rounding);
  -webkit-user-select: none;
  user-select: none;
  flex-flow: wrap;
  width: 100%;
  height: auto;
  transition: all 1s;
  display: flex;
  overflow: auto hidden;
}

.dragDropItem {
  padding: calc(var(--padding) / 2) var(--padding);
  border-radius: var(--rounding);
  box-sizing: border-box;
  color: var(--dark2);
  cursor: pointer;
  background-color: #fff;
  font-weight: 700;
  transition: all .2s;
  position: relative;
}

.dragDropItem span {
  background-color: var(--light2);
  color: var(--dark1);
  border-radius: 0 var(--rounding) var(--rounding) 0;
  margin-left: var(--padding);
  padding: calc(var(--padding) / 2) var(--padding);
  z-index: 2;
  top: 0%;
  left: var(--padding);
  position: relative;
  box-shadow: 0 0 10px #060606;
}

.dragDropItem.dragging {
  opacity: .5;
  transition: all .2s;
  transform: scale(1.05);
}

.dragDropItem.over {
  opacity: .5;
  transition: all .2s;
  transform: scale(.95);
}

#DragAndDrop-EmptyMessage {
  opacity: .2;
  gap: var(--padding);
  display: flex;
}

.settings {
  background-color: #0ff;
}

@media only screen and (width <= 1200px) {
  .searchContainer {
    flex-direction: column;
    height: 50vh;
  }

  .visualPianoContainer {
    flex: .7;
  }

  html {
    font-size: medium;
  }

  body {
    width: 100vw;
  }
}
